/*============================================================================
 * Normalize
 *============================================================================*/
* {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box; }

body {
  margin: 0; }

a {
  text-decoration: none; }

ul, ol {
  list-style: none; }

p, ul, ol {
  margin: 0;
  padding: 0; }

h1, h2, h3, h4, h5, h6 {
  margin: 0; }

img {
  max-width: 100%; }

input,
textarea,
button,
select {
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  border: none;
  background-color: transparent; }
  input:focus, input:active,
  textarea:focus,
  textarea:active,
  button:focus,
  button:active,
  select:focus,
  select:active {
    outline: none; }

textarea {
  resize: none; }

figure,
fieldset {
  padding: 0;
  margin: 0;
  border: none; }

/*============================================================================
 * Grid
 *============================================================================*/
.grid, .grid-uniform {
  *zoom: 1;
  width: calc(100% + 15px);
  margin-left: -15px; }
  .grid:after, .grid-uniform:after {
    content: "";
    display: table;
    clear: both; }
  .grid .grid__item, .grid-uniform .grid__item {
    padding-left: 15px;
    display: block; }

.grid__item {
  width: 100%;
  float: left; }

