/*============================================================================
 * flex
 *============================================================================*/
.grid-flex__item {
  padding-left: 15px;
  width: 100%; }

.grid-flex, .grid-flex--center {
  display: flex;
  flex-flow: row wrap;
  width: calc(100% + 15px);
  margin-left: -15px; }
  .grid-flex--center {
    align-items: center; }
    .grid-flex--center > .grid-flex__item {
      align-self: center; }

/**
 * Flow control
 */
.grid-flex--reverse {
  flex-flow: row-reverse wrap; }

@media screen and (min-width: 769px) {
  .grid-flex--reverse-md {
    flex-flow: row-reverse wrap; } }

.grid-flex--row {
  flex-flow: row nowrap; }

@media screen and (min-width: 769px) {
  .grid-flex--row-md {
    flex-flow: row nowrap; } }

