  max-width: 100%; }
  .__wrapper:after, .wrapper:after {
    content: "";
    display: table;
    clear: both; }
  .__wrapper .wrapper, .wrapper .wrapper {
    padding: 0; }

@media screen and (min-width: 769px) {
